<h1 id="welcome" tabindex="-1"><a class="header-anchor" href="#welcome" aria-hidden="true">#</a> Welcome!</h1>
<p>This is a website that I (<a href="https://ju.se/en/personinfo.html?sign=LarPet" target="_blank" rel="noopener noreferrer">Peter Larsson-Green<OutboundLink/></a>) use in my work at <a href="https://ju.se/en.html" target="_blank" rel="noopener noreferrer">Jönköping University<OutboundLink/></a> where I teach in various courses involving programming. Here I upload course material students can take part of in their studies. If you're curious, feel free to look around at the various courses and lectures.</p>
<h2 id="getting-help" tabindex="-1"><a class="header-anchor" href="#getting-help" aria-hidden="true">#</a> Getting help</h2>
<p>If you need help in any of the courses, <a href="https://github.com/PeppeL-G/course-material/issues" target="_blank" rel="noopener noreferrer">open an issue about it on GitHub<OutboundLink/></a>. I will try to answer your questions there as soon as possible. Students are also encouraged to answer each other's questions, but <strong>don't give away solutions for free</strong>. Instead just <strong>help each other as little possible</strong>, so the one asking the question will do most of her work on her own.</p>
